.uni-float {
  float: left;
  margin: 0 20px 20px 0;
}

.uni-float--whole {
  width: calc(100% - 20px);
  clear: left;
}

@media (min-width: 1000px) {
  .uni-float--half {
    width: calc(50% - 20px);
  }

  .uni-float--half:nth-child(2n+1) {
    clear: left;
  }
}

